// 1. Icons (lucide-react mocks)
const SendIcon = (props) => (<svg {...props} xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="m22 2-7 20-4-9-9-4Z"/><path d="M22 2 11 13"/></svg>);
const ListChecksIcon = (props) => (<svg {...props} xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="m3 15 2 2 4-4"/><path d="M21 13v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v8a2 2 0 0 0 2 2h4"/><path d="M21 16l-2 2-4-4"/></svg>);
const TicketCheckIcon = (props) => (<svg {...props} xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M18 9V7a2 2 0 0 0-2-2H4a2 2 0 0 0-2 2v10c0 1.1.9 2 2 2h4"/><path d="M19 16.5l-3.5-3.5"/><path d="M15 16.5h2"/><path d="M19 13v6a2 2 0 0 0 2 2h-6a2 2 0 0 0-2-2v-6a2 2 0 0 0 2-2h2a2 2 0 0 1 2 2Z"/></svg>);

// 2. UI Components (shadcn/ui mocks)
const Card = ({ children, className }) => <div className={`bg-white rounded-xl ${className}`}>{children}</div>;
const CardContent = ({ children, className }) => <div className={`p-4 ${className}`}>{children}</div>;
const Input = React.forwardRef(({ className, placeholder, ...props }, ref) => (
    <input ref={ref} className={`px-4 py-2 border rounded-lg ${className}`} placeholder={placeholder} {...props} />
));
const Button = ({ children, className, onClick, id, ...props }) => (
    <button id={id} onClick={onClick} className={`flex items-center justify-center rounded-lg font-semibold ${className}`} {...props}>
        {children}
    </button>
);
const Avatar = ({ children, className }) => <div className={`rounded-full overflow-hidden ${className}`}>{children}</div>;
const AvatarImage = ({ src, alt }) => <img src={src} alt={alt} className="w-full h-full object-cover" />;
const AvatarFallback = ({ children, className }) => <div className={`flex items-center justify-center w-full h-full ${className}`} style={{ backgroundColor: '#e0e7ff', color: '#4338ca' }}>{children}</div>; // Custom default background
const ScrollArea = ({ children, className }) => <div className={`overflow-y-auto ${className}`}>{children}</div>; // Simplified ScrollArea
const Scrollbar = ({ orientation }) => null; // Mocked

// 3. Context & Helpers
// Defined context structure to avoid runtime errors in UserBlock
const userContext = React.createContext({
    allMessages: [],
    setAllMessages: () => {},
    webContainerState: null,
    setWebContainerState: () => {},
});
const toast = { 
    error: (msg) => console.error(`[TOAST ERROR]: ${msg}`),
    success: (msg) => console.log(`[TOAST SUCCESS]: ${msg}`)
};

const getMessages = async (id) => {
    // Mock message data structure for demonstration
    const mockMessages = [
        { userMessage: "I need a responsive header and navigation bar using Tailwind.", ai: JSON.stringify([{ title: "Created index.html with header structure", status: "Completed" }, { title: "Added Tailwind CSS dependencies", status: "Completed" }]) },
        { userMessage: "Now, add a hero section with a call-to-action button, use a bright orange color for the button.", ai: JSON.stringify([{ title: "Defined Hero Section Layout in index.html", status: "Completed" }, { title: "Added CTA Button with primary orange color", status: "Pending" }, { title: "Styling hero section with responsiveness", status: "Pending" }]) },
    ];
    return { data: { messages: mockMessages } };
};
const axiosInstance = {
    post: (url, data) => {
        console.log(`[AXIOS MOCK] POST to ${url} with data:`, data);
        return new Promise(resolve => setTimeout(() => resolve({
            data: { message: "```xml\n<webcontainerState>Mock Updated State</webcontainerState>```" }
        }), 500));
    }
};
const xmltostring = async (xml) => {
    console.log("[HELPER MOCK] Converting XML to steps.");
    return [{ title: "Running file update steps...", status: "Pending" }];
};
const CreateMessage = async (id, user, ai) => console.log(`[HELPER MOCK] Message created for chat ${id}.`);
const UpdateWebContainerState = async (id, state) => console.log(`[HELPER MOCK] WebContainer state updated for chat ${id}.`);


// ====================================================================
// --- USER BLOCK COMPONENT (Original Logic Retained) ---
// ====================================================================

export function UserBlock({ id }: { id: string }) {
  // Destructure from mock context (or real context in your app)
  const { allMessages, setAllMessages, setWebContainerState, webContainerState } = useContext(userContext); 
  const [updateTheStates, setUpdateTheStates] = useState<Boolean>(true);
  const userprompt = useRef<HTMLInputElement | null>(null)
  const messagesEndRef = useRef<HTMLDivElement | null>(null);

  // Function to scroll to the bottom of the chat area
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  async function followBackPromptResponseFromAi(payload: string, prompt: string) {
    try {
      const response = await axiosInstance.post("/follow-back-prompt", { lastWebcontainerState: payload, userPrompt: prompt });

      // Removing the unwanted characters (retaining original slicing logic)
      const modifiedXML = response.data.message.slice(7, -4);
      await UpdateWebContainerState(id, modifiedXML)
      setWebContainerState(modifiedXML);
      
      const correctedXML = await xmltostring(modifiedXML);
      
      await CreateMessage(id, prompt, JSON.stringify(correctedXML))
      setUpdateTheStates(true);
    } catch (err) {
      console.log(err)
      toast.error("Ai Model is not Running");
    }
  }

  useEffect(() => {
    if (updateTheStates) {
      async function getwebtateAndMessages() {
        const messages = await getMessages(id);

        if (messages.data != null) {
          let allMessagesFromResponse = [];
          for (let i = 0; i < messages.data.messages.length; i++) {
            const newMessage = {
              user: messages.data.messages[i].userMessage,
              // Must parse the AI response since it's stored as a JSON string
              ai: JSON.parse(messages.data.messages[i].ai) 
            }
            allMessagesFromResponse.push(newMessage)
          }

          setAllMessages(allMessagesFromResponse);
        }
        
        scrollToBottom(); 
      }

      getwebtateAndMessages()
      setUpdateTheStates(false);
    }

  }, [updateTheStates, id, setAllMessages]);

  useEffect(() => {
    // Scroll to bottom whenever messages are updated
    scrollToBottom();
  }, [allMessages]);


  return (
    <div className="flex flex-col h-full w-full p-4 space-y-4">
      
      {/* --- Chat History Area --- */}
      {/* Set h-full on ScrollArea and its container to ensure it fills the flex-1 space */}
      <Card className="flex-1 min-h-0 shadow-lg border-gray-200">
        <ScrollArea className="h-full">
          <CardContent className="flex flex-col gap-6 p-4">
            {allMessages?.map((item, index) => (
              <div className="flex flex-col gap-4" key={index}>
                
                {/* User Message (Right Aligned) */}
                <div className="flex justify-end items-end gap-3">
                    {/* User Message Bubble */}
                    <div className="max-w-[80%] lg:max-w-[70%] bg-indigo-600 text-white p-3 rounded-xl rounded-br-sm shadow-md break-words">
                        <p className="text-sm font-medium">{item.user}</p>
                    </div>
                    {/* User Avatar */}
                    <Avatar className="h-8 w-8">
                        <AvatarImage src="https://ui.shadcn.com/avatars/03.png" alt="User Avatar" />
                        <AvatarFallback className="bg-indigo-200 text-indigo-700 font-bold text-sm">U</AvatarFallback>
                    </Avatar>
                </div>

                {/* AI Response (Left Aligned) */}
                <div className="flex justify-start items-start gap-3">
                    {/* AI Avatar */}
                    <Avatar className="h-8 w-8">
                        <AvatarImage src="https://ui.shadcn.com/avatars/05.png" alt="AI Avatar" />
                        <AvatarFallback className="bg-gray-200 text-gray-700 font-bold text-sm">AI</AvatarFallback>
                    </Avatar>
                    
                    {/* AI Steps Container */}
                    <div className="max-w-[80%] lg:max-w-[70%] flex flex-col gap-1 p-3 bg-gray-100 rounded-xl rounded-tl-sm shadow-sm border border-gray-200">
                        {item.ai.map((newItem, stepIndex) => (
                            <div 
                                key={stepIndex} 
                                className={`flex items-start gap-2 p-2 rounded-lg transition-colors 
                                    ${newItem.status === "Pending" 
                                        ? "bg-white border border-indigo-300 shadow-sm" 
                                        : "bg-green-50 border border-green-300"}`}
                            >
                                {/* Status Icon */}
                                <div className={`flex-shrink-0 mt-1 ${newItem.status === "Pending" ? "text-indigo-500" : "text-green-600"}`}>
                                    {newItem.status === "Pending" ? (
                                        <TicketCheckIcon className="h-4 w-4" />
                                    ) : (
                                        <ListChecksIcon className="h-4 w-4" />
                                    )}
                                </div>
                                {/* Step Title */}
                                <p className={`text-sm font-medium break-words ${newItem.status === "Pending" ? "text-gray-800" : "text-green-800"}`}>
                                    {newItem.title}
                                </p>
                            </div>
                        ))}
                    </div>
                </div>
              </div>
            ))}
            
            {/* Scroll Anchor */}
            <div ref={messagesEndRef} />
            
          </CardContent>
        </ScrollArea>
      </Card>
      
      {/* --- Input Area --- */}
      <Card className="w-full h-auto shadow-xl border-gray-300">
        <CardContent className="flex gap-4 p-4 items-center">
          <Input 
            ref={userprompt} 
            className="flex-1 h-12 text-base border-indigo-300 focus:border-indigo-500 rounded-lg shadow-inner"
            placeholder="Ask AI for help or provide instructions (e.g., 'Change the button color to red')" 
            onKeyDown={(e) => {
                if (e.key === 'Enter' && webContainerState != null && userprompt.current != null) {
                    e.preventDefault(); 
                    document.getElementById('send-prompt-button')?.click();
                }
            }}
          />
          <Button 
            id="send-prompt-button"
            onClick={async () => {
              if (webContainerState != null && userprompt.current != null && userprompt.current.value.trim() !== "") {
                console.log("re-prompting...")
                await followBackPromptResponseFromAi(webContainerState, userprompt.current.value)
                userprompt.current.value = ""
              } else if (userprompt.current != null && userprompt.current.value.trim() === "") {
                toast.error("Please enter a message to the AI.");
              }
            }} 
            className="h-12 w-12 bg-indigo-600 hover:bg-indigo-700 rounded-lg shadow-md transition-all active:scale-95"
          >
            <SendIcon className="h-5 w-5" />
          </Button>
        </CardContent>
      </Card>
    </div>
  );
}

// ====================================================================
// --- APP WRAPPER FOR DEMONSTRATION ---
// This component provides the necessary context and state for UserBlock to run.
// ====================================================================
export default function App() {
    const [allMessages, setAllMessages] = useState([]);
    const [webContainerState, setWebContainerState] = useState("initial_state");
    const contextValue = useMemo(() => ({
        allMessages, setAllMessages, webContainerState, setWebContainerState
    }), [allMessages, webContainerState]);

    return (
        <div className="h-screen w-full flex justify-center items-center p-4" style={{ fontFamily: 'Inter, sans-serif' }}>
            <div className="h-[90vh] w-full max-w-xl border border-gray-300 rounded-2xl shadow-2xl bg-white">
                <userContext.Provider value={contextValue}>
                    <UserBlock id="mock-chat-123" />
                </userContext.Provider>
            </div>
        </div>
    );
}
